/* jshint browser: true, devel: true, indent: 2, curly: true, eqeqeq: true, futurehostile: true, latedef: true, undef: true, unused: true */
/* global $, jQuery, document, Modernizr */

// DEV FUNCTION REMOVE FOR PROD

function l(data) {
  'use strict';
  console.log(data);
}

// VARIABLES

var scrollAnimationDuration = 400;

// CACHED

var $masonry = $('.js-masonry'),
  $window = $('.js-window'),

  $splash = $('#splash'),
  $home = $('#home'),
  $mainContent = $('#main-content'),

  $news = $('#news'),
  $backArrow = $('.back-arrow'),
  $newsLink = $('.js-news-link'),

  $noonVideo = $('#noon-video'),

  timeout;

// ROUTER
function router( hash ) {

    hash = hash.replace("#!/",'');

    if ( hash === 'news' ) {

      $splash.remove();
      $mainContent.show();
      $news.show();
      $masonry.masonry();
      $news.ScrollTo();
      $backArrow.removeClass('u-hidden');
      $newsLink.addClass('u-hidden');
      $('meta[property="og:title"]').attr('content','ARPA - News');
      $('title').html('ARPA - News');

    } else if ( hash === 'home' ) {

      $splash.remove();
      $home.ScrollTo();
      $backArrow.addClass('u-hidden');
      $newsLink.removeClass('u-hidden');
      $('meta[property="og:title"]').attr('content','ARPA');
      $('title').html('ARPA');

      timeout = setTimeout(function() {
        $mainContent.hide();
        $news.hide();
      }, scrollAnimationDuration);

    }

}

// LAYOUT FIXES
function setWindowSized() {
  $window.css({
    'min-height': $(window).height()
  });
}

function centerVideo() {
  var videoWidth = $noonVideo.width();
  var halfWindowWidth = ($(window).width() / 2);

  l(videoWidth);
  l(halfWindowWidth);

  var marginLeft = ((videoWidth - halfWindowWidth) / 2);

  l(marginLeft);

  $noonVideo.css({
    'margin-left': '-' + marginLeft + 'px'
  });
}

jQuery(document).ready(function () {
  'use strict';

  // LAYOUT
  setWindowSized();
  centerVideo();
  $(window).resize(function() {
    setWindowSized();
    centerVideo();
  });

  // ROUTER: on load
  if ( window.location.hash ) {
    router( window.location.hash );
  }

  // ROUTER: on change
  window.onhashchange = function () {
    router( window.location.hash );
  };

  // SCROLL EVENTS
  $(window).scroll(function() {
    // when you scroll back to top of the page set hash as home
    if ($(window).scrollTop() === 0) {
      $mainContent.hide();
      window.location.hash = '!/home';
    }
  });

  // CLICK EVENTS
  $('#splash-arpa').on('click', function() {
  	$splash.remove();
  	window.location.hash = '!/home';
  });

  $masonry.imagesLoaded( function() {
	  $masonry.masonry({
	    columnWidth: 1,
	    gutter: 0,
	    itemSelector: '.item',
      transitionDuration: 0
	  });
  });

});